.wrap {
  padding: 30px 0 0 0;
  margin-right: 70px;
}

.thermometer {
  display: flex;
}

.therm {
  font-size: 46px;
  line-height: 48px;
  font-weight: 700;
  text-align: center;
  margin: 0;
  padding-top: 5px;
}

.therm:after {
  content: '';
  display: block;
  width: 12px;
  height: 12px;
  border: 3px solid #000;
  border-radius: 20px;
  position: relative;
  bottom: 46px;
  left: 66px;
}

.minAndMax {
  display: flex;
  justify-content: space-between;
  width: 80px;
}

.minBlock,
.maxBlock {
  text-align: center;
}

.minNum,
.maxNum {
  font-weight: 700;
  font-size: 20px;
}

.minNum:after,
.maxNum:after {
  content: '';
  display: block;
  width: 7px;
  height: 7px;
  border: 2px solid #000;
  border-radius: 5px;
  position: relative;
  bottom: 25px;
  left: 29px;
}

@media screen and (max-width: 1024px) {
  .wrap {
    margin-right: 0px;
  }
}

@media screen and (max-width: 830px) {
  .img {
    width: 110px;
  }

  .wrap {
    margin-right: 0px;
  }

  .therm {
    margin-left: -20px;
    font-size: 40px;
    line-height: 42px;
    font-weight: 700;
  }

  .therm:after {
    content: '';
    display: block;
    width: 10px;
    height: 10px;
    border: 3px solid #000;
    border-radius: 20px;
    position: relative;
    bottom: 42px;
    left: 72px;
  }

  .minNum,
  .maxNum {
    font-size: 18px;
  }

  .minNum:after,
  .maxNum:after {
    content: '';
    display: block;
    width: 7px;
    height: 7px;
    border: 2px solid #000;
    border-radius: 5px;
    position: relative;
    bottom: 23px;
    left: 27px;
  }
}

@media screen and (max-width: 430px) {
  .wrap {
    margin-right: 40px;
  }

  .therm {
    margin-left: 0px;
  }

  .minNum,
  .maxNum {
    font-size: 20px;
  }

  .minNum:after,
  .maxNum:after {
    content: '';
    display: block;
    width: 7px;
    height: 7px;
    border: 2px solid #000;
    border-radius: 5px;
    position: relative;
    bottom: 24px;
    left: 28px;
  }
}
